<template>
  <section>
    <a :href="to" rel='noopener' target='_blank' v-if="isExternalLink"><slot /></a>
    <router-link v-else :to="to"><slot /></router-link>
  </section>
</template>

<script>
import { isExternal } from '@/utils'

export default {
  props: {
    to: {
      type: String,
      required: true
    }
  },
  computed: {
    isExternalLink() {
      return isExternal(this.to)
    }
  }
}
</script>



